<template>
    <div class="content">
        <div class="title">{{i18n.$t('common').success}}</div>
        <el-button type="success" round @click="jump">{{i18n.$t('home').app}}</el-button>
    </div>
</template>

<script>
import { useI18n } from "../../../i18nPlugin";
export default {
    data() {
        return {
            password: "",
            email: ""
        };
    },
    mounted() {
        (this.password = this.$route.query.password),
            (this.email = this.$route.query.email);
    },
    methods: {
        jump() {
            window.location.href =
                "qbaytek://www.qbaytek.com:80/customer/login?password=" +
                this.password +
                "&email=" +
                this.email;
        }
    },
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    }
};
</script>

<style lang="scss" scoped>
.content {
    background-color: #ffffff;
    width: 300px;
    margin: 70px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    padding: 10px;
    .title {
        font-weight: bold;
        font-size: 30px;
        margin-bottom: 15px;
    }
    .el-button {
        width: 50%;
    }
}
</style>